import * as React from "react";
import { styled, alpha } from "@mui/material/styles";

import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import InputBase from "@mui/material/InputBase";
import SearchIcon from "@mui/icons-material/Search";
import FilterAltOutlinedIcon from "@mui/icons-material/FilterAltOutlined";
import CloseIcon from "@mui/icons-material/Close";
import { useForm } from "react-hook-form";
import { Form, Field } from "react-final-form";
import {
  Badge,
  Button,
  Drawer,
  IconButton,
  InputLabel,
  MenuItem,
  Select,
  SwipeableDrawer,
  TextField,
  Typography,
} from "@mui/material";
import {
  clearDetailsActionCreator,
  fetchApiData,
  fetchTotalAnimeApiData,
} from "../../action/animeAction";
import { useDispatch, useSelector } from "react-redux";
import "./header.css";

import {clickDropdown,clickSound,clickCardSound} from '../../utils/sound';
const Search = styled("div")(({ theme }) => ({
  position: "relative",
  borderRadius: theme.shape.borderRadius,
  backgroundColor: alpha(theme.palette.common.white, 0.15),
  "&:hover": {
    backgroundColor: alpha(theme.palette.common.white, 0.25),
  },
  marginLeft: 0,
  width: "100%",
  [theme.breakpoints.up("sm")]: {
    marginLeft: theme.spacing(1),
    width: "auto",
  },
}));

const SearchIconWrapper = styled("div")(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: "100%",
  position: "absolute",
  pointerEvents: "none",
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
}));

const StyledField = styled(Field)`
  margin: 10px !important;
`;

const StyledTextField = styled(TextField)(({ theme }) => ({
  width: "300px !important", // Adjust this width as needed
  "& .MuiInputBase-input": {
    color: "white",
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)}) !important`,
    transition: theme.transitions.create("width"),
    [theme.breakpoints.up("sm")]: {
      width: "12ch !important",
      height: "1ch !important",
      "&:focus": {
        width: "20ch !important",
      },
    },
  },
}));

const CinemanimaHeading = styled("h1")`
  font-family: "Indie Flower", cursive;
  font-size: 2rem;
  color: #ffcc00;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  margin: 0;
  padding: 0;
`;

const StyledButton = styled("button")`
  .MuiBadge-badge {
    background-color: #c54a00;
  }
  svg {
    color: white;
  }
`;
const StyledToolbar = styled(Toolbar)`
  display: flex;
  align-items: center;
  justify-content: space-between;
  column-gap: 10px;
  #logo {
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      height: 50px;
      /* Add styles for larger screens */
    }
  }

  #logo:hover,
  img:hover,
  #text:hover {
    cursor: pointer;
  }

  @media (max-width: 600px) {
    /* Adjust styles for smaller screens */
    #logo {
      justify-content: start !important;
      flex-direction: column;
      align-items: start;
      img {
        height: 15px !important; /* Adjust the height for smaller screens */
      }
      #text {
        display: block;
        font-size: 15px;
      }
    }
  }
`;
const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: "inherit",
  width: "100%",
  "& .MuiInputBase-input": {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create("width"),
    [theme.breakpoints.up("sm")]: {
      width: "12ch",
      "&:focus": {
        width: "20ch",
      },
    },
  },
}));
function Header() {
  const totalAnime = useSelector((state) => state?.totalAnime?.data);
  const isLoading = useSelector((state) => state?.totalAnime?.loading);
  const dispatch = useDispatch();
  const move = function () {
    window.location.href = "/";
  };

  React.useEffect(() => {
    dispatch(fetchTotalAnimeApiData());
  });

  const handleOptionClick = () => {
    // console.log(searchValue);
    // console.log(searchValue);
    clickSound.play()
    dispatch(clearDetailsActionCreator());
    dispatch(fetchApiData(1, searchValue));
  };

  const [searchValue, setSearchValue] = React.useState("");
  const [filterValue, setFilterValue] = React.useState({});
  const currentPath = window.location.href;

  React.useEffect(() => {
    console.log(currentPath);
    // console.log(currentPath.includes("details"));
  }, [currentPath]);
  const [drawerOpen, setDrawerOpen] = React.useState(false);
  React.useEffect(() => {
    console.log(totalAnime);
    // console.log(totalAnime,"totalAnime`");
  }, [totalAnime]);
  const toggleDrawer = (open) => (event) => {
    
    if (
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }
    clickSound.play();
    setDrawerOpen(open);
  };
  const handleCloseDrawer = () => {
    clickDropdown.play();
    setDrawerOpen(false);
  };

  const handleBackdropClick = (event) => {
    clickDropdown.play()
    event.stopPropagation(); // Prevent click event from propagating to the parent elements
    event.preventDefault(); // Prevent default action of the event
  };

  const onSubmit = (values) => {
    clickCardSound.play();
    console.log(values); // Handle form submission data here
  };

  const ClearForm = () => {
    clickCardSound.play();
    console.log(); // Handle form submission data here
  };
  const handleCloseSelect = (input) => {
    input.onChange(""); // Clear the selected value
  };

  const getComonent = (label, key, values) => {
    return (
      <StyledField name={key} style={{ margin: "10px !important" }}>
        {({ input }) => (
          <Box mb={2}>
            <InputLabel shrink={false}>{label}</InputLabel>
            <Select
              {...input}
              fullWidth
              displayEmpty
              sx={{
                height: 40, // Adjust the height as needed
                borderRadius: 0, // Remove rounded corners
              }}
              endAdornment={
                input.value && (
                  <IconButton
                    size="small"
                    edge="end"
                    onClick={() => handleCloseSelect(input)}
                    sx={{
                      position: "absolute",
                      top: "50%",
                      transform: "translateY(-50%)",
                      right: 20,
                    }}
                  >
                    <CloseIcon
                      style={{
                        fontSize: "20px",
                        fontWeight: "500",
                        color: "orange",
                      }}
                    />
                  </IconButton>
                )
              }
            >
              <MenuItem value="" disabled>
                Select an option
              </MenuItem>
              {values?.map((data) => {
                return (
                  <MenuItem value={data.key} key={data.key}>
                    {data.value}
                  </MenuItem>
                );
              })}
            </Select>
          </Box>
        )}
      </StyledField>
    );
  };
  return (
    <>
      <Box
        sx={{ flexGrow: 1 }}
        style={{
          zIndex: "5",
          backdropFilter: "blur(2px)",
          position: "sticky",
          top: "0px",
        }}
      >
        <AppBar
          position="sticky"
          style={{
            boxShadow: "none",
            background: "transparent",
            position: "sticky",
          }}
        >
          <StyledToolbar>
            <div id="logo" onClick={move}>
              <img
                src={`${process.env.PUBLIC_URL}/logo_.png`}
                alt="Logo"
                style={{ height: "40px", marginRight: "5px" }}
              />
              <CinemanimaHeading id="text">Cinemanima</CinemanimaHeading>
            </div>
            <div style={{ display: "flex", columnGap: "10px" }}>
              <Search>
                <SearchIconWrapper>
                  <SearchIcon />
                </SearchIconWrapper>
                {currentPath.includes("details") ? (
                  // <Autocomplete
                  //   style={{ width: "100%" }}
                  //   id="search-input"
                  //   freeSolo
                  //   options={totalAnime}
                  //   getOptionLabel={(option) => option?.data?.title_english}

                  //   renderOption={(props, option) => (
                  //     <li {...props}>
                  //       <img
                  //         src={option?.img}
                  //         alt={option?.year}
                  //         style={{ width: 24  }}
                  //       />
                  //       {option?.year}
                  //     </li>
                  //   )}
                  //   renderInput={(params) => (
                  //     <StyledTextField
                  //       {...params}
                  //       placeholder="Search"
                  //       inputProps={{
                  //         ...params.inputProps,
                  //         "aria-label": "search",
                  //       }}
                  //     />
                  //   )}
                  //   onChange={(event, value) => {console.log(event.target.value); console.log(value)}}
                  //   // onInputChange={(event, newInputValue) => handleInputChange(newInputValue)}

                  // />
                  <></>
                ) : (
                  <StyledInputBase
                    placeholder="Search"
                    inputProps={{ "aria-label": "search" }}
                    onChange={(e) => {
                      setSearchValue(e.target.value);
                      if (!e?.target?.value || e?.target?.value == "") {
                        dispatch(fetchApiData(1));
                      }
                    }}
                  />
                )}
              </Search>
              <Button
                variant="contained"
                onClick={handleOptionClick}
                style={{ minWidth: "30px" }}
              >
                Go
              </Button>
            </div>
          </StyledToolbar>
        </AppBar>
      </Box>
      <StyledButton className="floating-icon1" onClick={toggleDrawer(true)}>
        <Badge badgeContent={4} color="primary">
          <FilterAltOutlinedIcon color="action" />
        </Badge>
      </StyledButton>
      <Drawer
        anchor="right"
        open={drawerOpen}
        onClose={toggleDrawer(false)}
        ModalProps={{
          BackdropProps: {
            onClick: handleBackdropClick,
          },
        }}
      >
        <Box sx={{ width: 250, padding: "16px" }}>
          <Form
            style={{}}
            onSubmit={onSubmit}
            render={({ handleSubmit }) => (
              <form onSubmit={handleSubmit}>
                <div
                  style={{ display: "flex", justifyContent: "space-between" ,marginBottom:'10px'}}
                >
                  <Typography style={{fontWeight:'600 !important',fontSize:'20px'}}>Filter</Typography>
                  <IconButton
                    size="small"
                    edge="end"
                    onClick={() => handleCloseDrawer()}
                  >
                    <CloseIcon
                      style={{
                        fontSize: "24px",
                        fontWeight: "500",
                        color: "orange",
                      }}
                    />
                  </IconButton>
                </div>
                {getComonent("Type", "Type", [
                  { key: "tv", value: "Rv", index: 0 },
                  { key: "movie", value: "Movie", index: 1 },
                  { key: "ova", value: "Ova", index: 2 },
                  { key: "special", value: "Special", index: 3 },
                  { key: "ona", value: "Ona", index: 4 },
                  { key: "music", value: "Music", index: 5 },
                  { key: "cm", value: "Cm", index: 6 },
                  { key: "pv", value: "Pv", index: 7 },
                  { key: "tv_special", value: "Tv Special", index: 8 },
                ])}
                {getComonent("Top Anime", "filter", [
                  { key: "airing", value: "Airing", index: 0 },
                  { key: "upcoming", value: "Upcoming", index: 1 },
                  { key: "bypopularity", value: "Bypopularity", index: 2 },
                  { key: "favorite", value: "Favorite", index: 3 },
                ])}
                {getComonent("Rating", "rating", [
                  { key: "g", value: "G - All Ages", index: 0 },
                  { key: "pg", value: "PG - Children", index: 1 },
                  { key: "pg13", value: "PG-13 - Teens 13 or older", index: 2 },
                  {
                    key: "r17",
                    value: "R - 17+ (violence & profanity)",
                    index: 3,
                  },
                ])}

                <div
                  style={{ display: "flex", justifyContent:'space-between' }}
                >
                  <Button variant="outlined" onClick={ClearForm}>clear</Button>
                  <Button type="submit" variant="contained" >
                    Submit
                  </Button>
                </div>
              </form>
            )}
          />
        </Box>
      </Drawer>
    </>
  );
}

export default Header;

